body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  display: grid;
  grid-template-rows: 150px auto;
  grid-row-gap: 50px;
  justify-items : center;
}
.nav {
  background : -webkit-linear-gradient(#8fd2f7, white);
  background : linear-gradient(#8fd2f7, white);
  width: 100%;
  text-align: center;
  font-size: 3em;
}

input{
  height: 45px;
  width: 300px;
  font-size: 1em;
  border: 1px solid #9E9E9E;
  padding: 0px 8px;
  background: #f6f6f6
}

button{
  margin-left : 20px;
  height: 50px;
  font-size: 1em;
  background: #8ad7fb;
  border: 2px solid white;
  width: 120px

}
